
#home {

	height: 100%;

	.content {

		@include sm {

			position: relative; }

		h1.welcome {

			margin: 30% 0 0 0;
			max-width: 480px;

			font-size: 18px;
			line-height: 26px;
			font-weight: 400;

			text-align: center;

			@include sm {

				margin: 0;

				font-family: $mf-thin;
				font-size: 25px;
				line-height: 32px;

				text-align: left; }

			@include ul {

				font-size: 36px;
				line-height: 48px;
				max-width: 600px; } }

		h2.shortie {

			display: none;

			margin: 40px 0;

			max-width: 550px;

			font-family: $mf-thin;
			font-size: 17px;
			font-weight: 400;
			line-height: 23px;

			@include sm {

				display: block; }

			@include ul {

				max-width: 700px;

				font-family: $mf-thin;
				font-size: 22px;
				line-height: 27px; } }

		a.btn {

			display: none;

			width: 150px;
			text-align: center;

			position: absolute;
			bottom: 8vh;
			left: 50%;
			transform: translateX(-50%);

			font-family: $mf-medium;
			font-size: 0.75em;

			padding: 0.8em 0;

			background: $background-colour;
			border-radius: 6px;

			@include sm {

				display: block;
				position: static;
				transform: none; } } }

	.bgImg {

		position: absolute;
		z-index: 1000;
		bottom: -8vh;
		right: -8vh;

		width: 100vw;
		height: 100vh;

		background-image: url("../img/bgProfile.png");
		background-repeat: no-repeat;
		background-size: 100% auto;
		background-position: bottom center;

		pointer-events: none;

		opacity: 1;

		@include sm {

			bottom: -8vw;
			right: -8vw;

			background-size: auto 100%;
			background-position: top right;

			opacity: .65; } } }
